interface RadioBoxProps{
    isActive: boolean;
    activeColor: "green" | "red"
}

const colors = {
    green: '#33CC95',
    red: '#E52E4D'
}

export const RadioBox = styled.button<RadioBoxProps> ` //dessa forma que se passar prop TS no Styled Components (conforme a documento)
    height: 4rem;
    border: 1px solid #d7d7d7;
    border-radius: 0.25rem;

    background: ${(props) => props.isActive   // no style componentes por ser CSS in JS  permitido o uso de funes; nest caso o parmetro props representa nativamente todas as propriedades deste componente, sendo possvel acess-las e aplicar regras de condio.
        ? transparentize(0.9, colors[props.activeColor])
        : 'transparent'
    };

    display: flex;
    align-items: center;
    justify-content: center;

    transition: border-color 0.2s;

    &:hover{
        border-color: ${darken(0.1, '#d7d7d7')}//L: escurea( darken) em 10% a cor #d7d7d7
    }


`