import React from 'react';
import ReactDOM from 'react-dom';
import { createServer, Model } from 'miragejs'
import { App } from './App';

createServer({

  //Para criar relao entre as rotas post e get 
  models: {
    transaction: Model, //model  um mdulo nativo do mirage e precisa ser importado
  },
  //O banco de dados sempre inica vazio, portanto criamos alguns dados ficticios para que a aplicao inicie de modo mais amigavel
  seeds(server){
    server.db.loadData({
      transactions: [ //nome do model no PLURAL
        {
          id: 1,
          title: "Freelance",
          type: 'deposit',
          category: 'dev',
          amount: 6000,
          createdAt: new Date('2022-02-12 09:00:00')
        },
        {
          id: 2,
          title: "Aluguel",
          type: 'withdraw',
          category: 'Casa',
          amount: 1100,
          createdAt: new Date('2022-02-14 11:00:00')
        }
      ]
    })
  },

  routes(){
    this.namespace = 'api' //significa dizer que todas as chamadas a serem feitas pelo mirage vo est A PARTIR do "/api" -> Observar o endereo no useEffect do TransactionsTable

    //metodo para pegar informao (get -> busca na API)
    this.get('/transactions', () =>{ //qdo houver uma chamada do tipo "GET" para "api/transactions", essa chamava vai devolver o que estiver demandado na funo 
      return this.schema.all('transaction') //o metodo GET vai retornar todas as transaes que h no bando de dados schema
    })

    //metodo de postagem (post -> insere na API)
    this.post('/transactions', (schema, request)=>{
      const data = JSON.parse(request.requestBody)

      return schema.create('transaction', data) // schema  o banco de dados ; primeiro argumento  qual o Model que estamos inserindo (no caso, transaction) e o segundo parmetro s as infos que iro ser add no model (no caso, data, proveniente do post)
      
    })
  }
})

ReactDOM.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>,
  document.getElementById('root')
);
